
import { defineComponent } from 'vue';
import { store } from '../../../store/store';
import { supabase } from '../../lib/supabaseClient';
import swal from 'sweetalert2';
import ContactIcon from '../../../assets/svg/TheContactIcon.vue';
import AddBtn from '../../buttons/TheAddBtn.vue';
import LoadingButton from '../../buttons/TheLoadingButton.vue';
import CloseModalButton from '../../buttons/TheCloseModalBtn.vue';

export default defineComponent({
  name: 'TheSettings.vue',
  components: {
    ContactIcon,
    AddBtn,
    LoadingButton,
    CloseModalButton,
  },

  data() {
    return {
      nBtn: true,
      loading: false,
      key: 1,
      username: store.getUsername(),
      contact: {
        name: '',
        phone: '',
        email: '',
      },
    };
  },

  methods: {
    async addNewContactBtn() {
      try {
        await supabase
          .from('contacts')
          .insert({
            username: this.username,
            name: this.contact.name,
            phone: this.contact.phone,
            email: this.contact.email,
          })
          .then(() => {
            swal
              .fire({
                icon: 'success',
                text: `Youve successfully saved the contact ${this.contact.name}.`,
                timer: 1500,
                showConfirmButton: false,
              })
              .then(() => {
                store.fetchContacts();
              });
          });
      } catch (error) {
        throw new Error('addNewContactBtn Error: ' + error);
      }
    },
  },
});
